body{
    background-color:#2E5478;
    font-family: sans-serif;
}
.todo-container{
    width:400px;
    min-height:350px;
    padding:10px;
   
    margin: auto;
    margin-top: 30px;
    text-align: center;

}
.todo-container h1{
    color: rgba(20, 13, 116, 0.7);
}
.input-container{
   
    padding: 20px;
    border-radius: 15px;
    box-sizing: border-box;
    margin-bottom: 20px;    
    background-color: #1f519c;
    box-shadow: inset -3px -3px 5px #ffffff70, inset 3px 3px 15px #00000070;
}
.input-container input{
    width:100%;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 5px;
    outline: none;
    border: none;
    background-color: #1f519c;
    box-shadow: inset -3px -3px 5px #ffffff70, inset 3px 3px 15px #00000070;
    color:gray;

}

ul{
    text-align: left;
    margin: 0;
    padding: 0;
  
}
ul li{
    box-sizing: border-box;
    list-style: none;
    padding: 10px;
    margin-bottom: 10px;
    background-color: #1f519c;
    border-radius: 5px;
    width:100%;
    box-shadow: inset -3px -3px 5px #ffffff70, inset 3px 3px 15px #00000070;
    color:grey;
    display: flex;
    justify-content: space-between;
}
li i{
    cursor: pointer;
}
#edit{

    margin-right: 10px;
}